.App {
  text-align: center;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  border-radius: 10px 10px 0 0;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.maquinas {
  margin: 2px;
  border: 1px dashed #000;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.maquinas button {
  font-size: 0.9em;
}

.edit {
  margin: 5px;
  border: 2px solid #000;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.edit input,
.edit select {
  width: 33.33%;
  padding: 10px;
  line-height: 1;
  font-size: 0.8rem;
  border: 0;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 0;
  background: #fff;
}

.edit .small {
  -ms-flex:1 1;
      flex:1 1;
}

.edit .medium {
  -ms-flex:2 1;
      flex:2 1;
}


.edit .large{
  -ms-flex: 3 1;
      flex: 3 1;
}

.edit input:focus,
.edit select:focus {
  outline: 0;
  background: #fef2de;
}
.edit input:last-of-type {
  width: 100%;
}
.edit button {
  width: 100%;
  border: 0;
}

.panel {
  -ms-flex:1 1;
      flex:1 1;
  border: 1px solid #000000;
  margin: 5px;
  border-radius: 10px;
  -webkit-box-shadow: 5px 5px 1px rgba(0, 0, 0, 0.3);
          box-shadow: 5px 5px 1px rgba(0, 0, 0, 0.3);
}

button,
input[type=submit] {
  text-transform: uppercase;
  background: none;
  border: 1px solid #000;
  font-weight: 600;
  font-size: 1.5rem;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  position: relative;
  z-index: 2;
  width: 100%;
}

.panel {
  margin: 5px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



